import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { MenuController, AlertController, LoadingController, NavController, ToastController, Platform, IonSlides } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { NavparamService } from '../navparam.service';
import { DataService } from '../data.service';

@Component({
  selector: 'app-search-product',
  templateUrl: './search-product.page.html',
  styleUrls: ['./search-product.page.scss'],
})
export class SearchProductPage implements OnInit {

  prods: any =[];
  key = this.navParamService.getKey();
  keys: any="";
  device = true; 
  web = true;
  nores = false;
  id: any = 0;

  constructor(
    private router: Router,
    private menuCtrl: MenuController,
    private dataService: DataService,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private storage: Storage, 
    private platform: Platform,
    private activatedRoute: ActivatedRoute,
    private navParamService: NavparamService
  ) { 
    this.device = this.platform.is('mobile');
    this.web =this.platform.is('desktop');
  }

  ngOnInit() {
    return new Promise(resolve =>{
      let body = {
        aksi: 'search',
        key: this.key
        
      }   
      this.accessProviders.postData(body, 'search.php').subscribe((res:any) => {
        
        if(res.success == true) {
           console.log(res.result);
           for(let datas of res.result){
            this.prods.push(datas);
          }
          console.log(this.prods);
          if(this.prods==0){
            this.nores=true;
          }else{
            this.nores=false;
          }
        } else {

        }
      });   
      
    });
  }
  back(){
    this.router.navigate(['/panel-vendor/1']);
  }
  itemDetails(product_id){
    console.log(product_id); 
    this.navParamService.setNavData(product_id);
    this.router.navigate(['/item-details']);
  }
  doRefresh(event) {
    console.log('Begin async operation');

    setTimeout(() => {
      console.log('Async operation has ended');
      event.target.complete();
    }, 2000);
  }
  

  async search() {
    if(this.keys==""){
      this.presentToast('Please type keyword');
    }else{
    const loader = await this.loadingController.create({
      message: 'Please wait',
    });
    loader.present();
    console.log(this.keys);
    this.navParamService.setKey(this.keys);
    this.prods.splice(0);

    

    return new Promise(resolve =>{
      let body = {
        aksi: 'search',
        key: this.keys
        
      }   
      this.accessProviders.postData(body, 'search.php').subscribe((res:any) => {
        
        if(res.success == true) {
          loader.dismiss();
           console.log(res.result);
           for(let datas of res.result){
            this.prods.push(datas);
          }
          console.log(this.prods);
          if(this.prods==0){
            this.nores=true;
          }else{
            this.nores=false;
          }

        } else {

        }
      });   
      
    });
  }
}

async presentToast(a) {
  const toast = await this.toastController.create({
    message: a,
    duration: 1500,
    position: 'bottom'
  });
  toast.present();
}  

}
